.Day {
  position: relative;
}
.Day.use-bar:before {
  content: '';
  position: absolute;
  right: 1px;
  margin-right: -2px;
  top: 9%;
  width: 1px;
  height: 82%; /* or 100px */
  border-right: 2px solid white;
}

.DatesContainer {
  position: absolute;
  left: 3px;
  top: 20px;
  color: black;
  font-size: 11px;
  font-family: cursive;
  z-index: 100;
  display: flex;
  flex-direction: column;
  height: 90%;
}

.DateContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.season {
  font-weight: 600;
}

.holiday p {
  text-align: left;
  padding-left: 2px;
}

.season .svg-inline--fa {
  height: 20px;
  width: 20px;
  padding-right: 1px;
  margin-left: -1px;
}

.birthday .svg-inline--fa {
  height: 12px;
  width: 12px;
  padding-right: 3px;
}

.SPRING {
  color: rgb(3, 75, 3);
}

.SUMMER {
  color: rgb(185, 105, 8);
}

.FALL {
  color: rgb(136, 37, 37);
}

.WINTER {
  color: rgb(40, 40, 148);
}

.birthday {
  position: absolute;
  bottom: 1px;
  font-size: 10px;
  margin-left: 4px;
  color: rgb(143, 18, 116);
}
